import React from "react";
import Layout from "./Layout";

const CheckView = () => {
  return (
    <Layout title="Check View Page">
      <div className="container">
        <div className="row">
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
        </div>
        <div className="row">
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
        </div>
        <div className="row">
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
          <div className="col-sm">One of three columns</div>
        </div>
      </div>
      <style jsx>{`
        .col-sm {
          background: black;
          color: white;
          padding: 50px;
          text-align: center;
          margin: 10px 10px 0px 0px;
          width: 100px;
          height: 300px;
          cursor: pointer;
        }
        .col-sm: active {
          border: 2px yellow;
        }
      `}</style>
    </Layout>
  );
};

export default CheckView;
